.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.is-hidden {
    opacity: 0;
    pointer-events: none;
}

.is-closed {
    display: none;

    @media screen and (min-width: 768px) {
        display: block;
    }
}